export default {
  username: [
    { required: true, message: '', trigger: 'blur' },
    { pattern: /^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{6,8}$/, message: ' + 6-8', trigger: 'blur' }],
  email: [
    { required: true, message: '', trigger: 'blur' },
    { type: 'email', message: '', trigger: 'blur' }],
  password: [
    { required: true, message: '', trigger: 'blur' },
    { pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d!.#*?&]{8,12}$/, message: ' ++ 8-12', trigger: 'blur' }],
  name: [
    { required: true, message: '', trigger: 'blur' }],
  nikname: [
    { required: true, message: '', trigger: 'blur' }
  ]
}