<template>
  <div class="blogm-user">

    <van-uploader v-if="avatar"
                  :preview-image="false"
                  :after-read="afterRead"
                  v-model="upload"
                  preview-size="5rem">
      <van-image width="5rem"
                 height="5rem"
                 round
                 :src="avatar" />
    </van-uploader>
    <van-image v-else
               round
               width="5rem"
               height="5rem"
               :src="require('@/assets/img/logo.jpg')" />

    <div class="blogm-users--content">
      <h2 class="blogm-users--title">{{title}}</h2>
      <BaseForm ref="userForm"
                :type="formType" />
      <p class="blogm-users--tag"
         v-if="tagValue"
         @click="changeType"
         v-html="tagValue"></p>
    </div>
    <div class="blogm-users--btns">
      <van-button type="info"
                  @click="onSubmit"
                  block>{{buttonName}}</van-button>
      <van-button type="warning"
                  v-if="formType === 'userInfo'"
                  @click="logout"
                  block></van-button>
    </div>
  </div>
</template>

<script>

import MODAL_DATA from '@/config/modal.config'
import BaseForm from '@/components/m/BaseForm'
import { mapGetters, mapActions } from 'vuex'
const BTN_NAME = {
  'login': '',
  'register': '',
  'userInfo': ''
}
const TAG_VALUE = {
  'login': '? <span></span>',
  'register': '? <span></span>',
  'userInfo': ''
}
export default {
  name: 'User',
  components: {
    BaseForm
  },
  props: {
    state: {
      type: String,
      default: 'login'
    }
  },
  data () {
    return {
      type: '',
      upload: [],
      fileBase: ''
    }
  },
  mounted () {

  },
  created () {
    let isLogin = this.$store.state.token
    let nikname = this.userInfo?.nikname
    if (isLogin && !nikname) {
      this.getUserInfo()
    }
  },
  computed: {

    tagValue () {
      return TAG_VALUE[this.formType]
    },
    requestType () {
      return this.formType === 'userInfo' ? 'putUserInfo' : this.formType
    },
    formType () {
      return this.nikname ? 'userInfo' : (this.type || this.state)
    },
    title () {
      return MODAL_DATA[this.formType]?.title
    },
    buttonName () {
      return BTN_NAME[this.formType] || ''
    },
    avatar () {
      return this.fileBase || this.userInfo?.avatar
    },
    nikname () {
      return this.userInfo?.nikname
    },
    userFileAction () {
      return process.env.VUE_APP_USER_UPLOAD_PATH
    },
    ...mapGetters(['userInfo'])
  },
  methods: {
    changeType () {
      this.type = (this.type || this.state) === 'login' ? 'register' : 'login'
    },
    async onSubmit () {
      let formWrap = this.$refs.userForm
      let formComponent = formWrap.$refs.form
      formComponent.validate().then(async () => {
        let formData = formWrap.form
        formData.avatar = this.avatar
        try {
          await this.$api({ type: this.requestType, data: formData })
          this.getUserInfo()
        } catch (err) {
          //
          formComponent.resetValidation()
        }
      }).catch(err => MediaError)
    },
    async afterRead (upload) {
      let formData = new FormData()
      formData.append('file', upload.file)
      try {
        let result = await this.$api({
          type: 'uploadUser', data: formData
        })
        this.fileBase = result.fileURL
      } catch (err) {
        console.log(err)
      }

    },
    ...mapActions(['logout', 'getUserInfo'])
  },
};
</script>

<style lang="stylus" >
.blogm-user
  display flex
  flex-direction column
  align-items center
  justify-content space-around
  padding 20px
  height 100%
  background-color #f9f9f9
.blogm-users--content
  width 100%
  padding 20px 0
  background-color #fff
.blogm-users--btns
  width 100%
  &>button+button
    margin-top 10px
.blogm-users--title
  font-size 22px
  font-weight 600
  text-align center
  line-height 1.8
.blogm-users--tag
  padding 1em 2em
  text-align right
  font-size 14px
  &>span
    color blue
</style>